@import "vars";
@import "functions";

@supports (display: grid) {
	// блоки на главной
	.main-news, .main-advantages {
		display: grid;
		grid-template-columns: persent(560) 50%; }
	.main-news {
		align-items: stretch; }

	.main-advantages {
		grid-gap: 25px 43px; }

	.main-news {
		grid-gap: 16px 43px; }

	.main-news__important, .main-advantages__list {
		@include size(auto);
		position: static;
		align-self: stretch; }

	.main-advantages__list {
		grid-row: 1 / 3; }

	.main-news__important {
		grid-row: 1 / 4; }

	.main-news__one, .main-advantages__app, .main-advantages__subscribe {
		width: auto;

		&:not(:last-child) {
			margin-bottom: 0; } }

