@media screen and (max-width: 1200px) {
	.main-news, .main-advantages {
		grid-gap: 16px 20px; } }
@media screen and (max-width: 660px) {
	.main-news, .main-advantages {
		display: grid;
		grid-template-columns: 100%; }
	.main-news__cont {
		margin-top: 30px; }
	.main-news__important {
		margin-bottom: 0; } }


